import { NigmaMessage } from '@/model/Message'
import { InjectionKey } from 'vue';
import { createStore, Store } from 'vuex'

export interface State {
  messageHistory: NigmaMessage[],
  sessionId: string
}

export const key: InjectionKey<Store<State>> = Symbol();


export const ADD_MESSAGE = "ADD_MESSAGE";
export const SEND_MESSAGE_REQ = "SEND_MESSAGE_REQ";
export const SEND_MESSAGE_RES = "SEND_MESSAGE_RES";
export const SEND_MESSAGE_ERR = "SEND_MESSAGE_ERR";
export const CREATE_SESSION_REQ = "CREATE_SESSION_REQ";
export const CREATE_SESSION_RES = "CREATE_SESSION_RES";
export const CREATE_SESSION_ERR = "CREATE_SESSION_ERR";


const loadObjFromLocal = function (objKey: string): any {
  return JSON.parse(localStorage.getItem(objKey)!);
}
const loadStringFromLocal = function (stringKey: string): any {
  return localStorage.getItem(stringKey);
}

export const store = createStore<State>({
  state: {
    messageHistory: loadObjFromLocal("messages") ? [...loadObjFromLocal("messages")] : [],
    sessionId: loadStringFromLocal("sessionId")
  },
  mutations: {
    [ADD_MESSAGE](state, message: NigmaMessage) {
      state.messageHistory = [...state.messageHistory, message];
    },
    [SEND_MESSAGE_REQ](state, request) {
      // console.log(request);
      //
    },
    [SEND_MESSAGE_RES](state, resMessages) {
      console.log(resMessages);
      state.messageHistory = [...state.messageHistory, ...resMessages];
    },
    [SEND_MESSAGE_ERR](state, error) {
      console.error(error);
      //
    },
    [CREATE_SESSION_REQ](state, request) {
      console.log(request);
      //
    },
    [CREATE_SESSION_RES](state, sessionId) {
      state.sessionId = sessionId;
    },
    [CREATE_SESSION_ERR](state, error) {
      console.error(error);
      //
    }
  },
  actions: {
    async createSession({ commit, state, dispatch }) {
      const head = { "Content-Type": "application/json" };
      const req: RequestInit = {
        method: 'POST',
        // headers: head,
        redirect: 'follow'
      };
      commit(CREATE_SESSION_REQ, req);
      try {
        const res = await (await fetch("https://i9y86y0v1l.execute-api.eu-west-2.amazonaws.com/prod/createSession", req)).json();
        const sessionId = res.sessionId;
        dispatch('saveStringOnLocal', { strKey: "sessionId", str: sessionId });
        commit(CREATE_SESSION_RES, sessionId);
        const resMessages = res.messages.map(
          (msg: NigmaMessage) => (new NigmaMessage({ text: msg.text, sender: 'other', image: msg.image, delay: msg.delay, customKeyboard: msg.customKeyboard, options: msg.options })));
        dispatch('saveObjOnLocal', { objKey: "messages", obj: [...state.messageHistory, ...resMessages] });
        commit(SEND_MESSAGE_RES, resMessages);
      } catch (error) {
        commit(CREATE_SESSION_ERR, error);
      }
    },

    async sendMessage({ commit, state, dispatch }, message) {
      if (state.sessionId === null) {
        throw new Error("sessionId null!");
      }
      commit(ADD_MESSAGE, { text: message, sender: 'me' });
      const head = { "Content-Type": "application/json" };
      const req: RequestInit = {
        method: 'POST',
        // headers: head,
        body: JSON.stringify({
          sessionId: state.sessionId,
          message,
        }),
        redirect: 'follow'
      };
      commit(SEND_MESSAGE_REQ, req);
      try {
        const res = await (await fetch("https://i9y86y0v1l.execute-api.eu-west-2.amazonaws.com/prod/elaborateMessage", req)).json();
        const resMessages = res.messages.map(
          (msg: NigmaMessage) => (new NigmaMessage({ text: msg.text, sender: 'other', image: msg.image, delay: msg.delay, customKeyboard: msg.customKeyboard, options: msg.options })));
        dispatch('saveObjOnLocal', { objKey: "messages", obj: [...state.messageHistory, ...resMessages] });
        commit(SEND_MESSAGE_RES, resMessages);
      } catch (error) {
        commit(SEND_MESSAGE_ERR, error);
      }

    },
    saveObjOnLocal(state, { objKey, obj }) {
      localStorage.setItem(objKey, JSON.stringify(obj));
    },
    saveStringOnLocal(state, { strKey, str }) {
      localStorage.setItem(strKey, str);
    }

  },
  modules: {
  }
})

